"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import { useRouter } from "next/navigation";

export default function HomeCleaningSlider({ category, services }) {
    const [slidesPerView, setSlidesPerView] = useState(4);
    const [start, setStart] = useState(0);
    const [loadingId, setLoadingId] = useState<number | null>(null);
    const router = useRouter();

    useEffect(() => {
        function handleResize() {
            if (window.innerWidth < 1024) {
                setSlidesPerView(3);
            } else {
                setSlidesPerView(4);
            }
        }
        handleResize();
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, []);

    const handlePrev = () => {
        setStart((prev) => Math.max(0, prev - 1));
    };

    const handleNext = () => {
        setStart((prev) => Math.min(services.length - slidesPerView, prev + 1));
    };

    const handleImageClick = (id: number) => {
        setLoadingId(id);
        setTimeout(() => {
            router.push(`/gosearch/${id}`);
        }, 400);
    };

    return (
        <div className="mb-10">
            <h2 className="text-2xl font-bold mb-2">{category}</h2>
            <p className="mb-6">
                Enjoy a clean and healthy home with our services and get rid of dust and bacteria!
            </p>

            <div className="relative flex items-center justify-center">
                <div className="relative inline-flex">
                    {/* زر السهم الأيسر */}
                    <button
                        onClick={handlePrev}
                        className="absolute left-0 top-1/2 -translate-y-1/2 z-20 p-2 rounded-full bg-gray-200 hover:bg-gray-300 disabled:opacity-50"
                        disabled={start === 0}
                    >
                        &#8592;
                    </button>

                    {/* الصور */}
                    <div className="flex gap-4 overflow-hidden px-4 sm:px-8">
                        {services.slice(start, start + slidesPerView).map((service, i) => (
                            <div
                                key={i}
                                className="bg-white rounded-xl shadow-md overflow-hidden min-w-[160px] max-w-[220px] sm:min-w-[200px] sm:max-w-[260px] md:min-w-[220px] md:max-w-[300px] flex-1 cursor-pointer relative"
                                onClick={() => handleImageClick(service.id)}
                            >
                                <div className="relative w-full h-40 sm:h-52 md:h-60">
                                    <Image
                                        src={service.img}
                                        alt={service.name}
                                        fill
                                        className="object-cover"
                                    />
                                    {loadingId === service.id && (
                                        <div className="absolute inset-0 bg-black bg-opacity-50 flex flex-col items-center justify-center z-10 backdrop-blur-sm rounded-lg">
                                            <div className="animate-spin rounded-full h-10 w-10 sm:h-12 sm:w-12 border-t-4 border-white border-gray-300"></div>
                                            <p className="text-white text-sm mt-2">جارٍ التحميل...</p>
                                        </div>
                                    )}
                                </div>
                                <div className="p-2 sm:p-4 text-center text-black font-semibold text-sm sm:text-base">
                                    {service.name}
                                </div>
                            </div>
                        ))}
                    </div>

                    {/* زر السهم الأيمن */}
                    <button
                        onClick={handleNext}
                        className="absolute right-0 top-1/2 -translate-y-1/2 z-20 p-2 rounded-full bg-gray-200 hover:bg-gray-300 disabled:opacity-50"
                        disabled={start >= services.length - slidesPerView}
                    >
                        &#8594;
                    </button>
                </div>
            </div>
        </div>
    );
}
